.tableCard
{
    width: 100%;
    padding: 18px;
    margin: 0px 15px;
    border-radius: 15px;
    box-shadow: none;
    background-color:#ffffff;
    cursor: pointer
}

h5{
    font-weight:400 !important;
}

.tableActionButtons {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
}

.tableActionButtons ul {
    list-style: none;
    display: inline-flex;
    padding: 0px;
    justify-content: space-between;
    background-color:  #e5e4e4;
    border-radius: 30px;
}

.tableActionButtons ul li{
    padding: 8px 10px;
    border-radius: 30px;
    font-size: 13px;
    width: 75px;
    text-align: center;
    color:#959595;
    cursor: pointer;
}

.tableActionButtons ul li:hover{
    background-color : #165816a6;
    color:#ffffff;
}

thead{
    color : #959595;
    font-size: 13px;
}

thead:nth-child(3){
    display:none;
}

tbody tr td:nth-child(5){
    color : #959595;
}

/* tbody tr td:nth-child(6){
   color:red;
} */


ul[data-test="pagination"]{
    margin: 0px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

ul[data-test="pagination"] a{
    background-color : #165816a6;
    color:#ffffff;
}

li[data-test="page-item"]{
    padding-left : 10px;

}

li[data-test="page-item"] span{
    font-size:18px;
}


div[data-test="dataTables_paginate"]{
    display:flex;
    width:50%
}

div[data-test="datatable-info"]{
    display: flex;
    align-items: flex-start;
    color: gray;
    font-size:15px;   
}

tbody tr td:nth-child(7){
    color : #959595;
    font-weight:800;
    font-size:20px;
}
